/* stylelint-disable */
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.header {
  padding-top: 36px;
}
.header__logo {
  display: flex;
}
.header__logo img {
  margin-top: 10px;
  width: 56px;
  height: 34px;
  margin-right: 12px;
}
.header__logo span {
  font-family: "Cedarville Cursive", cursive;
  font-weight: 400;
  font-size: 32px;
  line-height: 60px;
  color: #000;
}
.header__menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
}
.header__item {
  margin-right: 90px;
}
.header__item:last-child {
  margin-right: 0px;
}
.header__link {
  color: #444A4A;
  font-family: "Josefin Sans", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
}
.header__link_activ {
  color: #fff;
  padding: 13px 32px;
  background-color: #2D635E;
  border-radius: 6px;
}

.header-wrp {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.screen1 {
  padding-top: 50px;
}
.screen1__subtittle {
  margin: 0;
  padding: 0;
  font-family: "Cedarville Cursive", cursive;
  font-weight: 400;
  font-size: 28px;
  line-height: 53px;
  color: #444A4A;
}
.screen1__title {
  margin: 0;
  padding: 0;
  font-family: "Josefin Sans", sans-serif;
  font-weight: 700;
  margin-bottom: 26px;
  margin-top: 26px;
  font-size: 63px;
  line-height: 63px;
  color: #000;
}
.screen1__link {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 700;
  padding: 12px 35px;
  background-color: #2D635E;
  border-radius: 6px;
  color: #fff;
  font-size: 16px;
  line-height: 25px;
}
.screen1__picture {
  position: relative;
  background-color: #E3EBE7;
  width: 410px;
  height: 466px;
  overflow: unset;
}
.screen1 img {
  position: absolute;
  display: block;
  bottom: -50px;
  left: 30px;
  width: 344px;
  height: 373px;
  z-index: 2;
}
.screen1 span {
  position: absolute;
  bottom: 0px;
  left: 130px;
  font-family: "Cedarville Cursive", cursive;
  font-weight: 400;
  font-size: 72px;
  line-height: 136px;
  color: #444A4A;
}

.screen1-wrp {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.screen2 {
  padding-top: 150px;
}
.screen2__item {
  display: flex;
  margin-bottom: 157px;
}
.screen2__item:last-child {
  margin-bottom: 0px;
}
.screen2__img {
  position: relative;
}
.screen2__img:first-child {
  background-color: #FAE9E0;
  min-width: 380px;
  min-height: 287px;
  margin-right: 103px;
}
.screen2__img:first-child img {
  position: absolute;
  bottom: -57px;
  right: -15px;
  width: 364px;
  height: 262px;
}
.screen2__img:last-child {
  min-width: 410px;
  min-height: 276px;
  background-color: #E3EBE7;
  margin-left: 103px;
}
.screen2__img:last-child img {
  position: absolute;
  bottom: -10px;
  right: 80px;
  width: 236px;
  height: 320px;
}
.screen2__img span {
  font-family: "Cedarville Cursive", cursive;
  font-weight: 400;
  position: absolute;
  display: block;
  font-size: 28px;
  line-height: 53px;
  transform: rotate(-90deg);
}
.screen2__img span:first-child {
  top: 43px;
  left: -43px;
}
.screen2__title {
  margin: 0;
  padding: 0;
  font-family: "Josefin Sans", sans-serif;
  font-weight: 700;
  line-height: 38px;
  font-size: 38px;
  margin-bottom: 25px;
  color: #000;
}
.screen2__descr {
  margin: 0;
  padding: 0;
  font-family: "Alegreya Sans", sans-serif;
  font-weight: 400;
  font-size: 19px;
  line-height: 36px;
  margin-bottom: 15px;
  color: #444A4A;
}
.screen2__link {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 700;
  display: block;
  padding: 14px 30px;
  text-align: center;
  width: 180px;
  height: 48px;
  margin-top: 30px;
  color: #fff;
  font-size: 20px;
  line-height: 25px;
  background-color: #2D635E;
  border-radius: 6px;
}

.screen3 {
  margin-top: 75px;
  margin-bottom: 131px;
  background-color: rgba(200, 215, 213, 0.2156862745);
}
.screen3__item {
  padding: 45px 54px;
  background-color: #fff;
  border-radius: 20px;
  margin-top: 44px;
  margin-bottom: 44px;
  margin-right: 30px;
  text-align: center;
  -webkit-box-shadow: -2px 6px 18px 1px rgba(0, 0, 0, 0.26);
  box-shadow: -2px 6px 18px 1px rgba(0, 0, 0, 0.26);
}
.screen3__item:last-child {
  margin-right: 0px;
}
.screen3__link {
  font-size: 20px;
  line-height: 25px;
  color: #fff;
  background-color: #2D635E;
  padding: 15px 35px;
  border-radius: 6px;
}

.screen3-wrp {
  display: flex;
}

.screen__title {
  margin: 0;
  padding: 0;
  font-family: "Josefin Sans", sans-serif;
  font-weight: 700;
  font-size: 28px;
  line-height: 28px;
  margin-bottom: 25px;
}
.screen__descr {
  margin: 0;
  padding: 0;
  font-family: "Alegreya Sans", sans-serif;
  font-weight: 400;
  color: #444A4A;
  font-size: 19px;
  line-height: 36px;
  margin-bottom: 45px;
}

.footer__map img {
  max-width: 650px;
}
.footer__info {
  font-family: "Alegreya Sans", sans-serif;
  font-weight: 400;
}
.footer__info span {
  display: block;
  margin-bottom: 16px;
  font-size: 19px;
  line-height: 36px;
  color: #000;
}
.footer__title {
  margin: 0;
  padding: 0;
  font-family: "Josefin Sans", sans-serif;
  font-weight: 700;
  position: relative;
  margin-bottom: 21px;
  font-size: 32px;
  line-height: 45px;
  color: #444A4A;
}
.footer__title::before {
  content: "";
  position: absolute;
  top: 19px;
  left: -110px;
  background-color: #000;
  width: 90px;
  height: 2px;
}
.footer__copy {
  font-family: "Alegreya Sans", sans-serif;
  font-weight: 400;
  margin: 0;
  padding: 0;
  margin-top: 90px;
  text-align: center;
  color: #000000;
  font-size: 19px;
  line-height: 36px;
}

.footer-wrp {
  display: flex;
  justify-content: space-around;
}